import React from 'react';
import { useStore } from '@/utils/hooks/use-store';
const MenuContext = React.createContext({});
export const useMenuContext = () => React.useContext(MenuContext);
export const MenuProvider = (props) => {
    const [menus, dispatchMenus] = useStore('menus', {
        'Chord Player': {
            // 和弦库
            顺阶和弦: true,
        },
        'Chord Analyzer': true,
        'Chord Progressions': true,
        'Chord Collection': true,
        'Guitar Setting': {
            // 吉他设置
            'Board Visible': true,
            'Board Setting': true,
            'Instrument Setting': true, // 乐器设置
        },
        调式音阶1: true,
        调式音阶2: true,
        调式音阶3: true,
        调式音阶4: true,
    });
    const MenuValue = {
        menus,
        dispatchMenus,
    };
    return React.createElement(MenuContext.Provider, { value: MenuValue }, props.children);
};
