ckground: #333;\n\t}\n\n\t.sound-time-line {\n\t\tposition: absolute;\n\t\theight: 100%;\n\t\tleft: 42px;\n\t\twidth: $sound-margin * 2 + px;\n\t\ttop: 0px;\n\t\tz-index: 1;\n\t\tbackground: #eee8;\n\t\tvisibility: hidden;\n\n\t\t&.sound-time-visible {\n\t\t\tvisibility: visible;\n\t\t}\n\t}\n}\n\n.player-controller {\n\tdisplay: flex;\n\tflex-direction: row;\n\n\t.player-icon {\n\t\topacity: 0.8;\n\t\tmargin: $sound-margin;\n\t\t@include flex-center;\n\t}\n\n\t.player-range {\n\t\t@include flex-center(row);\n\t\tmargin: $sound-margin;\n\t\tpadding: 0 8px;\n\t\tjustify-content: start;\n\t\t.player-range-text {\n\t\t\tfont-size: 14px;\n\t\t\tfont-weight: 600;\n\t\t\topacity: 0.8;\n\t\t\twidth: 120px;\n\t\t\tletter-spacing: 0.5px;\n\t\t}\n\t\t.player-range-bpm {\n\t\t\tfont-size: 16px;\n\t\t}\n\t\tinput[type='range'] {\n\t\t\t-webkit-appearance: none;\n\t\t\tflex: 1;\n\t\t\theight: 4px;\n\t\t\tborder-radius: 2px;\n\t\t\toutline: none; /* 避免點選會有藍線或虛線 */\n\t\t\tbackground: #eee4;\n\t\t}\n\n\t\tinput[type='range']::-webkit-slider-thumb {\n\t\t\t-webkit-appearance: none;\n\t\t\twidth: 12px;\n\t\t\theight: 12px;\n\t\t\tbackground: #ccc;\n\t\t\tborder-radius: 50%;\n\t\t\ttransition: 0.2s; /* 點選放大時候的漸變時間 */\n\t\t}\n\n\t\tinput[type='range']:active::-webkit-slider-thumb {\n\t\t\twidth: 16px;\n\t\t\theight: 16px;\n\t\t}\n\t}\n}\n\n:global(.draggable-style) {\n\tbackground: $white-01 !important;\n}\n\n:global(.overed-activity-style) {\n\topacity: 0.1;\n}\n"],"sourceRoot":""}]);
