import React from 'react';
import { useBoardContext } from '../../board-provider';
import { instrumentConfig } from '@/utils/tone-player/tone.config';
import { Icon } from '@/components/icon';
import { optionsUIConfig, instrumentUIConfig } from './controller.config';
import { ControllerList } from '../controller';
import styles from './option-controller.module.scss';
export const OPTIONS_KEY = 'options';
export const INSTRUMENT_KEY = 'instrument';
export const BoardController = (props) => {
    return (React.createElement("div", { className: styles['container'] },
        React.createElement(BoardOptionsController, Object.assign({}, props)),
        React.createElement(BoardInstrumentController, Object.assign({}, props))));
};
/**
 * 指板显示内容选项
 * @param props
 * @returns
 */
export const BoardOptionsController = (props) => {
    const { boardOptions, dispatchBoardOptions } = useBoardContext();
    const handleClick = (option) => {
        dispatchBoardOptions({
            type: 'set',
            payload: Object.assign(Object.assign({}, boardOptions), { [option]: !boardOptions[option] }),
        });
    };
    const renderOptionItem = (option) => {
        const checkedItem = boardOptions[option]
            ? optionsUIConfig[option].checked
            : optionsUIConfig[option].unchecked;
        const uncheckedItem = !boardOptions[option]
            ? optionsUIConfig[option].checked
            : optionsUIConfig[option].unchecked;
        return (React.createElement("div", { className: styles['controller-inner'] },
            checkedItem.name_zh,
            React.createElement("div", { className: styles['controller-inner-unchecked'] }, uncheckedItem.name_zh)));
    };
    const list = Object.keys(boardOptions);
    // 默认展示选中的option，若未选中，则展示「isShowSemitone」
    const checkedList = list.filter((option) => boardOptions[option]);
    return (React.createElement(ControllerList, Object.assign({}, props, { list: list, onClickItem: handleClick, renderListItem: renderOptionItem, checkedItem: (option) => boardOptions[option], visibleItem: (option) => (checkedList.length > 0 ? false : option === 'isShowSemitone'), itemClassName: () => styles['controller-inner-option'] })));
};
/**
 * 指板播放乐器选项
 * @param props
 * @returns
 */
export const BoardInstrumentController = (props) => {
    const { instrument, dispatchInstrument } = useBoardContext();
    const renderInstrumentItem = (instrument) => {
        return (React.createElement("div", { className: styles['controller-inner'] },
            React.createElement("span", null, instrumentUIConfig[instrument].name_zh),
            React.createElement(Icon, { name: instrumentUIConfig[instrument].icon, size: 30 })));
    };
    const handleClick = (instrument) => {
        dispatchInstrument({ type: 'set', payload: instrument });
    };
    return (React.createElement(ControllerList, Object.assign({}, props, { list: Object.keys(instrumentConfig), onClickItem: handleClick, checkedItem: (item) => item === instrument, renderListItem: renderInstrumentItem })));
};
