import React, { useEffect, useMemo, useState } from 'react';
import { Board } from 'to-guitar';
import { TonePlayer } from '@/utils';
import { useStore } from '@/utils/hooks/use-store';
import { OPTIONS_KEY, INSTRUMENT_KEY } from '../board-controller';
/**
 * 吉他指板默认配置
 */
const defaultBoardOptions = {
    /**
     * 是否显示半音
     */
    isShowSemitone: false,
    /**
     * 半音是否以#标记
     */
    isSharpSemitone: true,
    /**
     * 是否音名显示
     */
    isNote: true,
    /**
     * 是否显示八度音高
     */
    hasLevel: false,
    /**
     * 是否显示吉他品记
     */
    hasTag: true,
};
/**
 * 吉他乐器默认配置
 */
const defaultInstrument = 'guitar-acoustic';
/**
 * 吉他播放器
 */
const player = new TonePlayer(defaultInstrument);
const BoardContext = React.createContext({});
/**
 * 获取吉他指板 Context
 */
export const useBoardContext = () => React.useContext(BoardContext);
export const BoardProvider = (props) => {
    const [guitarBoardOption, setGuitarBoardOption] = useState({});
    const [boardOptions, dispatchBoardOptions] = useStore(OPTIONS_KEY, defaultBoardOptions);
    const [instrument, dispatchInstrument] = useStore(INSTRUMENT_KEY, defaultInstrument);
    const [chord, setChord] = useState([]);
    const [chordTaps, setChordTaps] = useState(null);
    const [taps, setTaps] = useState([]);
    const [emphasison, setEmphasison] = useState([]);
    // 吉他指板实例化对象
    const guitar = useMemo(() => {
        const _board = new Board((board) => {
            setGuitarBoardOption(board);
        });
        // 实际上初始化 guitarBoardOption
        setGuitarBoardOption(_board.board);
        return _board;
    }, [setGuitarBoardOption]);
    // 切换乐器：加载乐器音源
    useEffect(() => {
        player.dispatchInstrument(instrument);
    }, [instrument]);
    const boardValue = {
        player,
        guitar,
        guitarBoardOption,
        boardOptions,
        dispatchBoardOptions,
        instrument,
        dispatchInstrument,
        chord,
        setChord,
        chordTaps,
        setChordTaps,
        taps,
        setTaps,
        emphasison,
        setEmphasison,
    };
    return React.createElement(BoardContext.Provider, { value: boardValue }, props.children);
};
