import React, { useState } from 'react';
import { PROGRESSIONS_KEY, progressionsConfig, } from '@/pages/chord-progressions/progressions.config';
import { useStore } from '@/utils/hooks/use-store';
const PlayerContext = React.createContext({});
/**
 * 获取播放器 Context
 */
export const usePlayerContext = () => React.useContext(PlayerContext);
export const PlayerProvider = (props) => {
    const [progressionIndex, setProgressionIndex] = useState(0);
    const [progressions, setProgressions] = useStore(PROGRESSIONS_KEY, progressionsConfig);
    const [soundList, setSoundList] = useState([]);
    const playerValue = {
        progressionIndex,
        setProgressionIndex,
        progressions,
        setProgressions,
        soundList,
        setSoundList,
    };
    return React.createElement(PlayerContext.Provider, { value: playerValue }, props.children);
};
